import React, { useState } from 'react'
import './footer.css'

function Footer() {
  const [isOpen, setIsOpen] = useState(false);
  const getInfoHeight = () => {
    if (window.innerWidth < 768) {
      return '230px';
    } else {
      return '103px';
    }
  };
  return (
    <footer id='footer'>
        <section className="sns_area">
          <article><img src="./images/main/icon/sns_facebook.png" alt=""></img></article>
          <article><img src="./images/main/icon/sns_instagram.png" alt=""></img></article>
          <article><img src="./images/main/icon/sns_youtube.png" alt=""></img></article>
          <article><img src="./images/main/icon/sns_kakao.png" alt=""></img></article>
        </section>
        <div className="footer_info">
          <div className="footer_toggle">
            <span className="copyright">
              <i className="copy"></i>
              <strong>AMOREPACIFIC</strong>
              CORPORATION. All rights reserved.
            </span>
            <div className={`toggle_btn ${isOpen?'is_open':''}`} onClick={() => {
              setIsOpen(!isOpen);
            }}>
            </div>
          </div>
          <div className="toggle_info" style={{ height: isOpen ? getInfoHeight() : '0px', overflow: 'hidden' }}>
            <div className="inner_info_container">
              <div className="inner_info">
                <span></span>
                <span></span>
                <span>UGC</span>
                <span><strong></strong></span>
                <span></span>
                <span></span>
              </div>
              <div className="inner_info">
                <p>() : 080-023-5454</p>
                <span>(~: AM 09:00~PM 06:00)</span>
              </div>
              <div className="footer_mark">
                <article><img src="./images/main/accessibility.png" alt=""></img></article>
              </div>
            </div>
          </div>
        </div>
        <div className="footer_container">
          <div className="footer_links">
            <div className="footer_links_txt">
              <p></p>
              <p></p>
              <p>UGC</p>
              <p><strong></strong></p>
              <p></p>
              <p></p>
              <p></p>
            </div>
            <div className="footer_img">
              <article className="access_icon"><img src="./images/main/accessibility.png" alt=""></img></article>
              <div className="footer_sns">
                <article><img src="./images/main/icon/sns_facebook.png" alt=""></img></article>
                <article><img src="./images/main/icon/sns_instagram.png" alt=""></img></article>
                <article><img src="./images/main/icon/sns_youtube.png" alt=""></img></article>
                <article><img src="./images/main/icon/sns_kakao.png" alt=""></img></article>
              </div>
            </div>
          </div>
          <div className="pc_footer_info">
            <div className="pc_cs_info">
              <span>() : 080-023-5454 </span>
              <span>(~: AM 09:00~PM 06:00)</span>
            </div>
            <div className="footer_copyright">
              <p>
                <i className="copy_icon"></i>
                <strong>AMOREPACIFIC</strong>
                 CORPORATION. All rights reserved.
              </p>
            </div>
          </div>
        </div>
    </footer>
  )
}

export default Footer
