import React, { useState } from 'react'
import SpaArticle from './SpaArticle'

export default function SectionSpa(props) {
    const {indexValue, setIndexValue, key, id, mainTitle, descTitle, descContent, pcImg, moImg, alt} = props;
    const [moreProgram, setMoreProgram] = useState(false);
    const [spaProgram, setSpaProgram] = useState('spa');

    /*  */
    const moreButton = (id) => {
        console.log(key);
        if(id === indexValue) {
            setMoreProgram( moreProgram => !moreProgram );
            setSpaProgram(id);
        } else {
            setIndexValue(id);
            setMoreProgram(true);
            setSpaProgram(id);
        }
    }
    

    return (
        <section className="program-section">
        <h2> { mainTitle } </h2>
        <div className="programs">
            <div className="program-title">   
                <picture>
                    <source srcSet={pcImg} media="(min-width: 600px)"/>
                    <img src={moImg} alt={alt} />
                </picture>
                <div className="program-desc">
                    <h3>{ descTitle }</h3>
                    <p>
                        {descContent[0]}
                        <br/>
                        {descContent[1]}
                    </p>
                </div>
                <button className={`moreBtn ${ moreProgram && (id===indexValue) ? '' : 'on'}`} onClick={ ()=>moreButton(id) } id={id} >
                    <span></span>
                    <span></span>
                </button>
            </div>
            <div className="programs-detail" style={ {display : `${ moreProgram && (id===indexValue) ? 'block' : 'none'}` } }>
                { articlePart[spaProgram].map( spaContent => (
                    <SpaArticle
                        key={spaContent.part+spaContent.key} 
                        part={spaContent.part} 
                        en={spaContent.en} 
                        kr={spaContent.kr} 
                        desc={spaContent.desc} 
                        price={spaContent.price} 
                    />)) }
            </div>    
        </div>
    </section>
    )
}





