import React, { useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import './ProductSort.css';
import ProductList from './ProductList';

function ProductSort() {
  const [select, setSelect]= useState(true);
  const [selectedSlide, setSelectedSlide] = useState('all');
  const handleSelectChange = (event) => {
    if (event.target.value === "type") {
      setSelect(true);
    } else if (event.target.value === "line") {
      setSelect(false);
    }
    setSelectedSlide('all');
  };
  const handleSlideClick = (id) => {
    setSelectedSlide(id);
  };

  const typeItems = [
    { id: "all", text: "" },
    { id: "gift", text: " " },
    { id: "cleansing", text: "" },
    { id: "boostessence", text: "" },
    { id: "skin", text: "" },
    { id: "lotion", text: "t" },
    { id: "essence_ample", text: "/" },
    { id: "eyecream", text: "" },
    { id: "cream", text: "" },
    { id: "sunblock", text: "" },
    { id: "mask", text: "/" },
  ];

  const lineItems = [
    { id: "all", text: "" },
    { id: "yunjo", text: "" },
    { id: "yunjoperfecting", text: "" },
    { id: "jaum", text: "" },
    { id: "jaumbright", text: " " },
    { id: "comfort", text: " " },
    { id: "perfect", text: " " },
    { id: "jinseol", text: "" },
    { id: "jjin", text: "" },
    { id: "etc", text: "" },
  ];

  return (
    <>
      <div className="products_sort">
          <div className="selectbox">
            <select id="sort_select" onChange={handleSelectChange}>
              <option value="type"> </option>
              <option value="line"> </option>
            </select>
            <span><i className="fas fa-chevron-down"></i></span>
          </div>
          <Swiper 
            className="swiper" 
            id={select ? "type" : "line"}
            slidesPerView="auto" 
            freeMode={true}
          >
            {(select ? typeItems : lineItems).map((item, index) => (
              <SwiperSlide 
                key={index} 
                className={item.id === selectedSlide ? "selected" : ""} 
                id={item.id}
                onClick={() => handleSlideClick(item.id)} //   
              >
                {item.text}
              </SwiperSlide>
            ))}
          </Swiper>
      </div>
      <ProductList selectedId={selectedSlide}/>
    </>
  )
}

export default ProductSort