import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation ,Autoplay } from 'swiper/modules';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import './recommend_sec.css'
import 'swiper/css';

function Recommend_sec() {
    const [autoplayEnabled, setAutoplayEnabled] = useState(true);
    const [paused, setPaused] = useState(false);
    const swiperRef = useRef(null);

    const toggleAutoplay = () => {
        const newPaused = !paused; //  paused  
        setPaused(newPaused); // paused  
        setAutoplayEnabled(!newPaused); //   

        const swiper = swiperRef.current;
        if (swiper) {
            if (newPaused) {
                swiper.autoplay.stop(); // 
            } else {
                swiper.autoplay.start(); // 
            }
        }
    };

  return (
    <section className="recommend w1440">
        <h1> </h1>
        <Swiper
            modules={[Pagination, Navigation, Autoplay]}
            loop={true}
            speed={1400}
            onSwiper={swiper => (swiperRef.current = swiper)} //   
                autoplay={{
                delay: 3000,
                disableOnInteraction: false,
                enabled: autoplayEnabled, //      
            }}
            navigation={{
                nextEl: '.swiper_button_next', //   
                prevEl: '.swiper_button_prev', //   
            }}
            pagination= {{
                el: ".recommend_progressbar",
                type: "progressbar",
            }}
            breakpoints= {{
                360: {
                  slidesPerView: 1,
                  spaceBetween: 0,
                  navigation: {
                    prevEl: '.swiper_button_prev',
                    nextEl: '.swiper_button_next',
                  },
                },
                768: {
                  slidesPerView: 3,
                  spaceBetween: 20,
                  slidesPerGroup: 3,
                },
                1024: {
                  slidesPerView: 3,
                  spaceBetween: 20,
                  slidesPerGroup: 3,
                },
            }}
            className="swiper recommend_card">
            <ul className="swiper-wrapper recommend_card_swiper">
                <SwiperSlide className="swiper-slide">
                    <img src="./images/main/recommend_slide01.jpg" alt=""></img>
                    <div className="recommend_txt">
                        <h2> <span>6</span></h2>
                        <p>First Care Activating Serum VI</p>
                        <p className="price">140,000</p>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="swiper-slide">
                    <img src="./images/main/recommend_slide02.jpg" alt=""></img>
                    <div className="recommend_txt">
                        <h2></h2>
                        <p>Concentrated Ginseng Renewing Cream EX</p>
                        <p className="price">270,000</p>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="swiper-slide">
                    <img src="./images/main/recommend_slide03.jpg" alt=""></img>
                    <div className="recommend_txt">
                        <h2></h2>
                        <p>Concentrated Ginseng Renewing Serum EX</p>
                        <p className="price">220,000</p>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="swiper-slide">
                    <img src="./images/main/recommend_slide04.jpg" alt=""></img>
                    <div className="recommend_txt">
                        <h2></h2>
                        <p>Gentle Cleansing Oil</p>
                        <p className="price">47,000</p>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="swiper-slide">
                    <img src="./images/main/recommend_slide05.jpg" alt=""></img>
                    <div className="recommend_txt">
                        <h2></h2>
                        <p>Gentle Cleansing Foam</p>
                        <p className="price">42,000</p>
                    </div>
                </SwiperSlide>
                <SwiperSlide className="swiper-slide">
                    <img src="./images/main/recommend_slide06.jpg" alt=""></img>
                    <div className="recommend_txt">
                        <h2></h2>
                        <p>White Ginseng Radiance Refining Mask</p>
                        <p className="price">72,000</p>
                    </div>
                </SwiperSlide>
            </ul>
            <button type="button" className="swiper-button swiper-button-sec2 swiper_button_prev"></button>
            <button type="button" className="swiper-button swiper-button-sec2 swiper_button_next"></button>
            <div className={`pagination_container recommend_container ${autoplayEnabled ? '' : 'paused'}`}>
                <div className="recommend_container_inner">
                    <div className="swiper-pagination recommend_progressbar"></div>
                    <button type="button" className={`recommend_pause ${autoplayEnabled ? '' : 'active'}`} onClick={toggleAutoplay}>
                        <span></span>
                    </button>
                </div>
            </div>
        </Swiper>
    </section>
  )
}

export default Recommend_sec
